<template>
    <div class="turntableLayer" id="turntableLayer" @touchmove.prevent @scroll.prevent @click="close">
        <div class="turntablePrompt" @click.stop>
            <img class="closeIcon" src="../assets/img/turntable_close.png" alt="" @click="close">
            <div class="turntableC">
                <div class="turntableMain" ref="turntableMain" :style="{'transform':'rotate('+turntableDeg+'deg)'}">
                    <canvas class="canvas_turntable" ref="canvas">抱歉！浏览器不支持。</canvas>
                    <ul class="prizeContainer">
                        <li v-for="(item,index) in prizeDataArr" :key="index"
                            :style="{'transform':'rotate('+(item.id * 45 + 22.5)+'deg)'}">{{item.text}}
                        </li>
                    </ul>
                </div>
                <div class="turntablePointerC">
                    <div class="turntablePointer" @click="lotteryDraw">点我抽奖</div>
                </div>
            </div>
            <div class="activityIntroC">
                <div class="title">活动介绍</div>
                <ul class="introList">
                    <li>• 本次抽奖活动由伊利金领冠官方举办</li>
                    <li>• 本次活动共送出一等奖10个，二等奖25个，三等奖900个</li>
                    <li>• 中奖观众请如实填写地址信息，用于礼品邮寄</li>
                </ul>
                <div class="remark">备注：本次直播抽奖方式以微博公布为准</div>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'turntable',
    data() {
      return {
        prizeDataArr: [
          {
            id: '0',
            text: '谢谢参与',
            imgUrl: '',
          },
          {
            id: '1',
            text: '一等奖',
            imgUrl: '',
          },
          {
            id: '2',
            text: '谢谢参与',
            imgUrl: '',
          },
          {
            id: '3',
            text: '二等奖',
            imgUrl: '',
          },
          {
            id: '4',
            text: '谢谢参与',
            imgUrl: '',
          },
          {
            id: '5',
            text: '一等奖',
            imgUrl: '',
          },
          {
            id: '6',
            text: '谢谢参与',
            imgUrl: '',
          },
          {
            id: '7',
            text: '三等奖',
            imgUrl: '',
          },
        ],
        blockNum: 8,
        turntableDeg: 0,
        transitionEndName: '',
        cssPrefix: '',
        eventPrefix: '',
        vendors: {
          '': '',
          Webkit: 'webkit',
          Moz: '',
          O: 'o',
          ms: 'ms',
        },
        rotateLoading: false,
        lotteryDrawFlag: true, // 是否能抽奖，默认可以
        lotteryResult: {
          level: '',
          prizeName: '',
        },
        remainDrawLotteryNum: 0,
        canDrawLotteryNum: 0,

      };
    },
    methods: {
      webSiteSniffer() {
        let testEle = document.createElement('p');
        let vendors = this.vendors;
        Object.keys(vendors).some((vendor) => {
          console.log('vendor_s', vendor + (vendor ? 'T' : 't') + 'ransitionProperty');
          if (testEle.style[vendor + (vendor ? 'T' : 't') + 'ransitionProperty'] !== undefined) {
            this.cssPrefix = vendor ? '-' + vendor.toLowerCase() + '-' : '';
            this.eventPrefix = vendors[vendor];
            console.log('cssPrefix', this.cssPrefix);
            console.log('eventPrefix', this.eventPrefix);
            return true;
          }
        });
      },

      /**
       * Bind events to elements
       * @param {Object}    ele    HTML Object
       * @param {Event}     event  Event to detach
       * @param {Function}  fn     Callback function
       */
      bind(ele, event, fn) {
        if (typeof addEventListener === 'function') {
          ele.addEventListener(event, fn, false);  // false 指定在事件捕获还在冒泡阶段执行  默认 false 冒泡
        } else if (ele.attachEvent) {
          ele.attachEvent('on' + event, fn);
        }
      },

      /**
       * [兼容事件前缀]
       * @param  {[type]} name [description]
       * @return {[type]}      [description]
       */
      normalizeEvent(name) {
        return this.eventPrefix ? this.eventPrefix + name : name.toLowerCase();
      },
      close() {
        if (!this.rotateLoading) {
          this.$emit('closeTurntable');
        }
      },
      lotteryDraw() {
        if (this.remainDrawLotteryNum <= 0 || this.canDrawLotteryNum <= 0) {
          return false;
        }
        if (this.rotateLoading) return false;
        this.rotateLoading = true;
        this.getLotteryResult(() => {
          let deg = this.turntableDeg;
          let blockAngle = 2 * Math.PI / this.blockNum;
          let prizeId;
          console.log('this.lotteryResult.level.toString()', this.lotteryResult.level.toString());
          switch (this.lotteryResult.level.toString()) {
            case '1':
              prizeId = this.getRandomID([1, 5]);
              break;
            case '2':
              prizeId = 3;
              break;
            case '3':
              prizeId = 7;
              break;
            case '0':
            default:
              prizeId = this.getRandomID([0, 2, 4, 6]);
              break;
          }

          deg = deg + 360 * 10 - prizeId * blockAngle * 360 / (2 * Math.PI) - deg % 360 - 22.5;
          this.turntableDeg = deg;
          console.log('prizeId', prizeId);
          console.log('this.turntableDeg', this.turntableDeg);
          // TODO: 监听transitionEnd
          this.bind(this.$refs.turntableMain, this.transitionEndName, () => {
            setTimeout(() => {
              this.rotateLoading = false;
              // TODO: 如果中奖 需要追加一个恭喜中奖的弹框
              this.close();
              this.showlotteryPrompt();
            }, 2000);

          });
        });
      },
      getLotteryResult(callback) {
        let url = '/api.php/api/Api/luckyDraw';
        this.$ajax(url, {}, res => {
          if (res.data.code == 200) {
            this.lotteryResult.level = res.data.data.levelName;
            this.lotteryResult.prizeName = res.data.data.name;
            console.log('this.lotteryResult', this.lotteryResult);
            this.canDrawLotteryNum--;
            this.remainDrawLotteryNum--;
            localStorage.setItem('RemainDrawLotteryNum', this.remainDrawLotteryNum);
            localStorage.setItem('CanDrawLotteryNum', this.canDrawLotteryNum);
            localStorage.setItem('prizeLevel', this.lotteryResult.level);
            localStorage.setItem('prizeName', this.lotteryResult.prizeName);
            if (callback) callback();
          }
        });
      },
      showlotteryPrompt() {
        this.$emit('showlotteryPrompt');
      },
      drawturntable() {
        //获取屏幕的宽度
        let canvas = this.$refs.canvas;
        let turntableMain = this.$refs.turntableMain;

        canvas.setAttribute('width', turntableMain.offsetWidth + 'px');
        canvas.setAttribute('height', turntableMain.offsetHeight + 'px');

        let r = Math.ceil(canvas.offsetWidth / 2);
        console.log('R', r);

        // var  clientWidth = document.documentElement.clientWidth;
        // var canvasWidth = Math.floor(clientWidth*339/700);

        if (!canvas.getContext) {
          alert('抱歉！浏览器不支持。');
        } else {
          let ctx = canvas.getContext('2d');
          let blockNum = 8;
          let blockAngle = 2 * Math.PI / blockNum;
          ctx.translate(r, r);
          ctx.lineWidth = 0.2;
          ctx.strokeStyle = '#E42A25';
          for (var i = 0; i < blockNum; i++) {
            if (i % 2 == 0) {
              ctx.fillStyle = '#F7D468';
            } else {
              ctx.fillStyle = '#E42A25';
            }
            ctx.beginPath();
            ctx.moveTo(0, 0);
            ctx.arc(0, 0, r, i * blockAngle, (i + 1) * blockAngle, false); // 圆心 半径 起始角 结束角 false:逆时针
            // ctx.lineTo(0, 0);
            ctx.stroke();
            ctx.fill();
            ctx.closePath();
          }
        }
      },
      getDrawLotteryNum() {
        this.remainDrawLotteryNum = localStorage.getItem('RemainDrawLotteryNum');
        this.canDrawLotteryNum = localStorage.getItem('CanDrawLotteryNum');
      },
      getRandomID(arr) {
        let _num = Math.round(Math.random(0, 1) * (arr.length - 1));
        console.log('_num', _num);
        return arr[_num];
      },
    },
    mounted() {
      this.drawturntable();
      //TODO: 嗅探
      this.webSiteSniffer();
      this.transitionEndName = this.normalizeEvent('TransitionEnd');
      this.getDrawLotteryNum();
    },
  };
</script>

<style lang="less" scoped>
    #turntableLayer {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 99;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.7);
        display: flex;
        justify-content: center;
        align-items: center;
        .turntablePrompt {
            position: relative;
            width: 7rem;
            height: 10rem;
            background-color: red;
            background: url("../assets/img/turntablePrompt.png") no-repeat center center;
            background-size: cover;
            border-radius: 10px;
            overflow: hidden;
            .turntableC {
                position: relative;
                margin: 2.87rem auto 0;
                width: 4.27rem;
                height: 4.27rem;
                background: url("../assets/img/turntableBack.png") no-repeat center center;
                background-size: cover;
                display: flex;
                align-items: center;
                justify-content: center;
                .turntableMain {
                    width: 3.4rem;
                    height: 3.4rem;
                    border-radius: 100%;
                    overflow: hidden;
                    position: relative;
                    transition: all 6s ease;
                    /*background-color: yellow;*/
                    .canvas_turntable {
                        position: absolute;
                        /*width: 100%;*/
                        /*height: 100%;*/
                    }
                    .prizeContainer {
                        position: absolute;
                        top: 0.1rem;
                        left: 50%;
                        margin: 0 0 0 -0.2rem;
                        width: 0.4rem;
                        height: 3.2rem;
                        & > li {
                            position: absolute;
                            top: 0;
                            left: 0;
                            text-align: center;
                            width: 100%;
                            height: 100%;
                            overflow: hidden;
                            padding-top: 0.02rem;
                            box-sizing: border-box;
                            line-height: 1;
                            color: #ffffff;
                            transform: rotate(22.5deg);
                        }
                    }
                }
                .turntablePointerC {
                    position: absolute;
                    width: 3.4rem;
                    height: 3.4rem;
                    border-radius: 100%;
                    overflow: hidden;
                    z-index: 99;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .turntablePointer {
                        /*position: absolute;*/
                        /*top: 0.78rem;*/
                        /*left: 50%;*/
                        /*margin-left: -0.525rem;*/
                        margin-top: -0.3rem;
                        width: 1.05rem;
                        height: 1.44rem;
                        background: url("../assets/img/pointerIcon.png") no-repeat center center;
                        background-size: cover;

                        font-family: PingFangSC-Regular;
                        font-size: 0.31rem;
                        font-weight: 400;
                        color: rgba(255, 46, 44, 1);

                        box-sizing: border-box;
                        padding-top: 0.46rem;
                        padding-left: 4px;
                        text-align: center;
                        letter-spacing: 0.1rem;

                        user-select: none;
                        -webkit-user-select: none;
                        -moz-user-select: none;
                        -ms-user-select: none
                    }
                }
            }
            .activityIntroC {
                margin: 0.32rem auto 0;
                width: 6.60rem;
                height: 2.5rem;
                background-color: rgba(118, 18, 18, 0.9);
                border-top-left-radius: 0.12rem;
                border-top-right-radius: 0.12rem;
                .title {
                    padding: 0.1rem 0;
                    text-align: center;
                    color: #F4BB58;
                    font-size: 0.3rem;
                    font-weight: bold;
                }
                .introList {
                    margin: 0 auto;
                    width: 6.31rem;
                    height: 1.42rem;
                    overflow-y: auto;
                    overflow-x: hidden;
                    & > li {
                        text-align: center;
                        height: 0.32rem;
                        line-height: 0.32rem;
                        font-size: 0.22rem;
                        font-weight: 400;
                        font-family: PingFangSC-Regular;
                        border-radius: 0.16rem;
                        color: rgba(244, 187, 88, 1);
                        margin-bottom: 0.1rem;
                        background-color: rgba(89, 10, 10, 1);
                    }
                }
                .remark {
                    text-align: center;
                    font-size: 0.24rem;
                    font-family: PingFangSC-Light;
                    font-weight: 300;
                    color: rgba(244, 187, 88, 1);
                }

            }
            .closeIcon {
                position: absolute;
                top: 0.22rem;
                left: 0.22rem;
                width: 0.27rem;
                height: 0.27rem;
            }
        }

    }
</style>


