<template>
    <div class="submitLayer" id="submitLayer" @touchmove.prevent>
        <div class="submitPrompt" @click.stop>
            <div class="closeIconC">
                <img class="closeIcon" src="../assets/img/submitPrompt_close.png" alt="" @click="close">
            </div>
            <div class="submitTitle">信息填写</div>
            <ul class="submitList">
                <li><label for="name">姓名</label><input id="name" type="text" placeholder="请输入姓名" v-model="submitData.name" maxlength="6"></li>
                <li><label for="number">电话</label><input id="number" type="number" placeholder="请输入电话" v-model="submitData.phone" maxlength="11"></li>
                <li><label for="birthday" style="width: 2rem">宝宝出生日期</label><input id="birthday" type="date" v-model="submitData.birthday" style="width: 3.85rem"></li>
                <li><label for="birthday">所属地区</label><VDistpicker class="VDistpicker" wrapper @selected="getDistrict" :province="submitData.district.province" :city="submitData.district.city" :area="submitData.district.area"></VDistpicker></li>
                <li><label for="address">地址</label><input id="address" type="text" placeholder="请输入地址" v-model="submitData.address"></li>
            </ul>
            <div class="submitBtnC">
                <button class="submitBtn" :class="{'act':submitDataFlag}" @click="submit">提交订单</button>
            </div>
        </div>
    </div>
</template>

<script>
  import VDistpicker from 'v-distpicker'
  export default {
    name: 'submitPrompt',
    data() {
      return {
        submitData: {
          name: '',
          phone: '',
          birthday: '1970-01-01',
          district: {
            province: '',
            city: '',
            area: ''
          },
          address: ''
        },
        submitDataFlag: false
      }
    },
    methods: {
      close() {
        if (confirm('放弃本次信息填写，本次领奖将失效哦!')) {
          this.$emit('closeSubmitPrompt');
          this.$emit('resetLotteryView');
        }
      },
      submit() {
        console.log('submitDataFlag',this.submitDataFlag);
        console.log('submitData',this.submitData);
        if(!this.submitDataFlag) return false;
        let submitData = this.submitData;
        let data = {
          consignee: submitData.name,
          phone: submitData.phone,
          birthday: submitData.birthday,
          district: submitData.district.province + submitData.district.city + submitData.district.area,
          address: submitData.address
        };
        let url = '/api.php/api/Api/luckyDrawBingAddress';
        this.$ajax(url, data, res => {
          if (res.data.code == 200) {
            this.$Tool.prompt('提交成功!');
            this.saveSubmitData();
            this.$emit('resetLotteryView');
            this.$emit('closeSubmitPrompt');
          }
        });
      },
      getDistrict(value) {
        console.log('value',value);
        this.submitData.district.province = value.province.value;
        this.submitData.district.city = value.city.value;
        this.submitData.district.area = value.area.value;
        console.log('this.submitData.district',this.submitData.district);
      },
      saveSubmitData() {
        localStorage.setItem('SubmitData',JSON.stringify(this.submitData));
      },
      getSubmitData() {
        this.submitData = localStorage.getItem('SubmitData') ? {...JSON.parse(localStorage.getItem('SubmitData'))} : this.submitData;
      }
    },
    watch: {
      submitData: {
        handler(val, oldVal){
          let count = 0;
          let keyArr = Object.keys(val);
          // console.log('submitData',val);
          // console.log('keyArr',keyArr);
          // console.log('count',count);
          for (let i=0, len=keyArr.length; i < len; i++) {
            let key = keyArr[i], value = val[key];
              if (value === '') {
                count++;
                break;
              }
              if (key === 'district' && (value.area === '区' || value.area == '')) {
                count++;
                break;
              }
              if(key === 'phone' && !this.$Tool.checkMobile(value)) {
                count++;
                break;
              }
            }
          this.submitDataFlag = count > 0 ? false : true;
        },
        deep:true
      }
    },
    components: {
      VDistpicker
    },
    mounted() {
      this.getSubmitData();
    }
  };
</script>

<style lang="less" scoped>
    #submitLayer {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 99;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.7);
        display: flex;
        justify-content: center;
        align-items: center;
        .submitPrompt {
            position: relative;
            width: 7rem;
            height: 6.6rem;
            background-color: rgba(255,255,255,1);
            border-radius: 0.30rem;
            .closeIconC {
                position: absolute;
                top: 0.35rem;
                left: 0.37rem;
                width: 0.22rem;
                height: 0.23rem;
                /*background-color: cyan;*/
                .closeIcon {
                    width: 0.22rem;
                    height: 0.23rem;
                }
            }
            .submitTitle {
                margin: 0 auto;
                width: 6.2rem;
                text-align: center;
                color: #000000;
                font-size: 0.36rem;
                /*font-weight: bold;*/
                padding: 0.24rem 0 0.24rem 0;
                letter-spacing: 0.1rem;
                border-bottom: 1px solid #CACACA;
            }
            .submitList {
                margin: 0 auto;
                width: 6.2rem;
                & > li {
                    /*text-align: center;*/
                    height: 0.79rem;
                    border-bottom: 1px solid #CACACA;
                    line-height: 0.79rem;
                    font-family: PingFangSC-Regular;
                    font-size: 0.30rem;
                    color: rgba(45,8,8,1);
                    label {
                        display: inline-block;
                        height: 100%;
                        width: 1.6rem;
                    }
                    input {
                        height: 100%;
                        width: 4.2rem;
                        font-size: 0.30rem;
                        border: 0;
                    }
                    input[type='date'] {
                        height: 98%;
                    }
                    .VDistpicker {
                        float: right;
                        /*display: inline-block;*/
                        width: 4.6rem;
                        overflow: hidden;
                        /deep/ select {
                            width: 1.45rem;
                            font-size: 0.3rem;
                            border: 1px solid #d8d8d8;
                            border-radius: 4px;
                            /*background-color: #f5f7fa;*/
                            /*border-color: #e4e7ed;*/
                            /*color: #c0c4cc;*/
                            /*cursor: not-allowed;*/
                        }
                    }
                }
            }
            .submitBtnC {
                margin: 0.5rem auto 0;
                text-align: center;
                button {
                    width: 3.19rem;
                    height: 0.58rem;
                    background-color: rgba(243,62,62,0.5);
                    border: 0;
                    border-radius: 0.29rem;
                    text-align: center;
                    font-size: 0.30rem;
                    font-family: PingFangSC-Regular;
                    font-weight: 400;
                    color: rgba(255,255,255,1);
                }
                button.act {
                    background-color: rgba(243,62,62,1);
                }
            }
        }
    }
</style>


