<template>
    <div class="lotteryLayer" id="lotteryLayer"  @touchmove.prevent @scroll.prevent>
        <div class="lotteryPrompt" @click.stop>
            <div class="title">{{titleTextList[titleType]}}</div>
            <div class="lotteryHint">{{lotteryHintList[lotteryHintType]}}</div>
            <div class="handleHint">{{handleHintList[handleHintType]}}</div>
            <div class="buttonBtn"><button @click="toNext">知道了</button></div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'lotteryPrompt',
    data() {
      return {
        titleType: '',
        titleTextList: {
          fail: '很抱歉',
          win: '中奖了'
        },
        lotteryHintType: '',
        lotteryHintList: {
          '0': '谢谢参与',
          '1': '一等奖',
          '2': '二等奖',
          '3': '三等奖'
        },
        handleHintType: '',
        handleHintList: {
          fail: '最重要的就是开心哈',
          win: '恭喜您中奖了，请进行信息填写，不然将错过领奖哦!'
        },
        currentPrizeInfo: {
          level: '',
          name: ''
        }
      }
    },
    methods: {
      close() { this.$emit('closelotteryPrompt'); },
      toNext() {
        this.$emit('closelotteryPrompt');
        if (this.currentPrizeInfo.level > 0) {
          this.$emit('showSubmitPrompt');
        }else {
          this.$emit('resetLotteryView');
        }
      }
    },
    mounted() {
       this.currentPrizeInfo.level = localStorage.getItem('prizeLevel');
       this.currentPrizeInfo.name = localStorage.getItem('prizeName');
       if (this.currentPrizeInfo.level > 0) {
         this.titleType = 'win';
         this.handleHintType = 'win';
         this.handleHintList.win = `恭喜您获得${this.currentPrizeInfo.name}，请进行信息填写，不然将错过领奖哦!`
       }else {
         this.titleType = 'fail';
         this.handleHintType = 'fail';
       }
       this.lotteryHintType = this.currentPrizeInfo.level.toString();
    }
  };
</script>

<style lang="less" scoped>
    #lotteryLayer {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 99;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.7);
        display: flex;
        justify-content: center;
        align-items: center;
        .lotteryPrompt {
            width: 7rem;
            height: 5.97rem;
            background: url("../assets/img/lotteryPrompt.png") no-repeat center center;
            background-size: cover;
            overflow: hidden;
            -moz-user-select: none;
            -khtml-user-select: none;
            user-select: none;
            font-family:PingFangSC-Regular;
            position: relative;
            .title {
                margin-top: 0.33rem;
                height: 0.44rem;
                line-height: 0.44rem;
                text-align: center;
                font-size: 0.44rem;
                color: #DFAA3A;
                letter-spacing: 0.2rem;
            }
            .lotteryHint {
                height: 0.62rem;
                line-height: 0.62rem;
                margin-top: 0.93rem;
                text-align: center;
                font-size: 0.62rem;
                font-weight: bold;
                color: #DFAA3A;
                letter-spacing: 0.2rem;
            }
            .handleHint {
                width: 6rem;
                margin: 1.24rem auto 0;
                line-height: 1.2;
                font-size: 0.36rem;
                font-weight: 400;
                text-align: center;
                color: rgba(255,255,255,1);
                letter-spacing: 0.05rem;
            }
            .buttonBtn {
                position: absolute;
                left: 0;
                bottom: 0.5rem;
                width: 100%;
                text-align: center;
                button {
                    width: 3.4rem;
                    height: 0.60rem;
                    line-height: 0.60rem;
                    background-color: rgba(223,170,58,1);
                    border-radius: 0.34rem;
                    border: 0;
                    font-size: 0.4rem;
                    letter-spacing: 0.1rem;
                    color: #ffffff;
                }
            }
        }
    }
</style>


