<template>
    <div id="mainPage">
        <div class="header">
            <div class="headerLogoC">
                <img class="leftLogo" src="../assets/img/yili_logo.png">
                <img class="rightLogo" src="../assets/img/jdg_logon.png">
            </div>
        </div>
        <div class="liveC">
            <div class="liveTitleC"></div>
            <div class="online_text">目前观看人数</div>
            <div class="online_number">{{watchNumbers}}人</div>
            <div class="video_container">
                <img :src="videoObj.src" v-if="videoObj.type === 'image' && videoObj.src" alt="">
                <video id="liveVideo" controls="controls"  preload="auto" @ended="videoEnd"  x5-playsinline="true"  webkit-playsinline="true" playsinline v-if="videoObj.type !== 'image' && videoObj.src" v-show="showVideoFlag">
                    <source :src="videoObj.src">
                </video>
            </div>
            <!--TODO: 关注可以加一个终端判断，微信以外浏览器不用展示-->
            <div class="attentC">
                <div class="attentBtn" @click="toPublicPage" v-if="publicInfo.url">关注+</div>
            </div>
        </div>
        <div class="lotteryC">
            <div class="lotteryTitleC"></div>
            <div class="lotteryIntroC">
                <div class="smallRotate" @click="showTurntable" :class="{'act':canDrawLotteryNum}">
                    <div class="countDown" v-if="!canDrawLotteryNum" @click.stop><span>{{showLotteryTime.minutes}}</span>:<span>{{showLotteryTime.seconds}}</span></div>
                    <div class="mark" v-if="canDrawLotteryNum" @click.stop>{{canDrawLotteryNum}}</div>
                    <div class="lotteryHint" v-if="canDrawLotteryNum || remainDrawLotteryNum <= 0" @click.stop>{{remainDrawLotteryNum > 0 ? '恭喜您获得一次抽奖' : '抽奖机会用完啦~'}}</div>
                </div>
                <div class="prizeIntro">
                    <p>奖励说明:</p>
                    <ul class="prizeList">
                        <li class="prizeItem" v-for="(item,index) in prizeList" :key="index">• {{item.level}}：{{item.name}} {{item.num}}个</li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="memoryShareC">
            <div class="memoryTitleC"></div>
            <ul class="share_segment flex">
                <li class="picture_amount">照片数量 &nbsp;&nbsp;{{photoTotal}}</li>
                <li class="new_upload" @click="newUpload">最新上传</li>
                <li class="time_sort" @click="timeSort">时间排序</li>
            </ul>
            <div class="share_content_c" ref="share_content_c" @scroll.stop="shareContentScroll">
                <ul class="share_content">
                    <li v-for="(item,index) in showImgList" @click="showImg(item)" :key="index">
                        <!--<img :src="item.imgFlag ? item.imageUrl: '/static/img/yili_logo.44694e1.png'">-->
                        <img :src="item.imageUrl">
                    </li>
                </ul>
                <mugen-scroll :handler="fetchData" :shouldHandle="!loading" scroll-container="share_content_c" class="loadingStatus">
                    {{(showImgList.length > pageSize - 1) && showImgList.length > 6 ? loadingText : ''}}
                    <!--<div class="loadingStatus" v-show="showImgList.length > pageSize - 1">{{loadingText}}</div>-->
                </mugen-scroll>
            </div>
            <div class="slide_up_c" @click="scrollNextPage" v-show="slideUpFlag"></div>
            <div class="share_footer"></div>
        </div>
        <div class="QRcodeC">
            <div class="QRcodeContent">
                <img :src="qRcodeInfo.qrLink" v-if="qRcodeInfo.qrLink">
            </div>
            <div class="QRcodeText">扫描二维码</div>
            <div class="QRcodeText_two">获取更多信息</div>
        </div>
        <div class="sloganC">
            <p v-for="(item,index) in qRcodeInfo.content" :key="index"> • {{item}}</p>
        </div>
        <div class="footerC">
            <div class="phoneNumber">电话：{{phoneNumber}}</div>
            <div class="email">邮箱：{{email}}</div>
        </div>
        <div class="imgShowContainer" v-show="bigImgShow" @touchmove.prevent @click="closeImgShow">
            <img :src="bigImgSrc">
        </div>
        <div class="returnTop" @click="returnTop" v-show="returnTopFlag">返回</div>
        <Turntable v-if="!turntableFlag" @closeTurntable="closeTurntable" @showlotteryPrompt="showlotteryPrompt"></Turntable>
        <SubmitPrompt v-show="submitPromptFlag" @closeSubmitPrompt="closeSubmitPrompt" @resetLotteryView="resetLotteryView"></SubmitPrompt>
        <LotteryPrompt v-if="lotteryPromptFlag" @closelotteryPrompt="closelotteryPrompt" @resetLotteryView="resetLotteryView" @showSubmitPrompt="showSubmitPrompt"></LotteryPrompt>
        <img src="../assets/img/turntablePrompt.png" alt="" style="display: none">
        <img src="../assets/img/turntableBack.png" alt="" style="display: none">
        <img src="../assets/img/lotteryPrompt.png" alt="" style="display: none">
    </div>
</template>

<script>
  import MugenScroll from 'vue-mugen-scroll';
  import wx from 'weixin-js-sdk';
  import Vue from 'vue';
  import Turntable from '../components/turntable'
  import SubmitPrompt from '../components/submitPrompt'
  import LotteryPrompt from '../components/lotteryPrompt'
  // import videojs from 'video.js'
  // import 'videojs-contrib-hls'
  /*
  * 1.获取抽奖次数与倒计时
  * 2.视频获取接口(已有，未直播，需增加图片展示)
  * 3.获取用户人数(已有)
  * 4.获取图片列表(已有)
  * 5.获取关注公众号链接
  * 6.获取二维码
  * 7.获取抽奖结果
  * 8.提交用户信息
  * 9.奖励说明(看需要，可前端固定写)
  *
  * */
  export default {
    name: 'mainPage',
    data() {
      return {
        videoObj: {
          type: '',
          protocolType: '',
          src: ''
        },
        watchNumbers: 0, // 观看人数
        watchTimer: null, // watch定时器
        watchDelayTime: 5000 * 6,
        wacthErrorTime: 20000,
        showImgList: [], // 图片容器
        photoTotal: 0, // 图片总数
        indexPage: 1, // 当前加载页面
        pageSize: 10, // 每页数量
        loadTimer: null, // 加载定时器
        loading: false, // 加载状态
        loadingText: '', // 加载文本
        imgListType: 'new', // 当前图片列表类型（最新上传new、时间正序asc、时间倒序desc）
        lastImgListType: '', // 上次图片列表类型
        bigImgSrc: '', // 大图路径
        bigImgShow: false, // 大图展示状态
        returnTopFlag: false, // 置顶展示状态
        showVideoFlag: true,
        slideUpFlag: true,
        turntableFlag: false,
        submitPromptFlag: false,
        lotteryPromptFlag: false,
        lotteryTimer: null,
        canDrawLotteryNum: 0,
        remainDrawLotteryNum: 3,
        currentLotteryTime: 0,
        lotteryTimeList: {
          '3': 5 * 60 * 1000,
          '2': 10 * 60 * 1000,
          '1': 10 * 60 * 1000
        },
        showLotteryTime: {
          days: '',
          hours: '',
          minutes: '--',
          seconds: '--'
        },
        publicInfo: {
          url: '',
          name: ''
        },
        qRcodeInfo: {
          qrContent: '',
          qrLink: '',
          content: ''
        },
        prizeList: [
          {
            level: '一等奖',
            name: '美的 三重防辐射设计微波炉',
            num: '10'
          },
          {
            level: '二等奖',
            name: '费雪 进口环保材质爬行垫',
            num: '25'
          },
          {
            level: '三等奖',
            name: '哺乳巾 法国设计师设计',
            num: '900'
          }
        ],
        phoneNumber: '400-800-1111',
        email: 'yilibabyclub@yili.com'
      };
    },
    methods: {
      init() {
        // 获取视频数据
        this.getVideoData();
        // 获取观看人数数据
        this.getWatchNumbers();
        // TODO: 获取用户彩票状态
        this.getUserLotteryStatus();
        // TODO: 首次获取图片列表
        this.fetchData();
        // TODO: 获取二维码信息
        this.getFootInfo();
        // TODO: 获取公众号地址
        this.getPublicAddress();
        // 获取微信数据
        this.wxOnShare();
      },
      toPublicPage() {
        window.location.href = this.publicInfo.url;
      },
      scrollNextPage() {
        let $share_content_c = this.$refs.share_content_c;
        if ($share_content_c.scrollHeight > $share_content_c.offsetHeight) {
          $share_content_c.scrollTop += $share_content_c.offsetHeight;
        }
        // console.log('$share_content_c.offsetHeight',$share_content_c.offsetHeight);
        // console.log('$share_content_c.scrollHeight',$share_content_c.scrollHeight);
        // console.log('$share_content_c',$share_content_c.scrollTop);
      },
      shareContentScroll(e) {
        let $share_content_c = this.$refs.share_content_c;
        if ($share_content_c.scrollTop > 100) {
          this.returnTopFlag = true;
        }else {
          this.returnTopFlag = false;
        }
        if ($share_content_c.scrollTop > $share_content_c.scrollHeight - $share_content_c.offsetHeight - 100) {
          this.slideUpFlag = false;
        }else {
          this.slideUpFlag = true;
        }
        // console.log('e',e.srcElement.scrollTop);
        // console.log('e.srcElement.offsetHeight',e.srcElement.offsetHeight);
        // console.log('e.srcElement.scrollHeight',e.srcElement.scrollHeight)
      },
      fetchData() {
        console.log('fetchData');
        if (this.loading) return false;
        this.loading = true;
        this.loadingText = '加载中...';
        this.lastImgListType = this.imgListType;
        if (this.indexPage === 1) {
          this.getImageList({clearImageList: true});
        } else {
          if (this.loadTimer !== null) {
            clearTimeout(this.loadTimer);
            this.loadTimer = null;
          }
          this.loadTimer = setTimeout(() => {
            this.getImageList({clearImageList: false});
          }, 500);
        }
      },
      getImageList(options) {
        this.$ajax('/api.php/api/Api/getImage', {
          page: this.indexPage,
          pageSize: this.pageSize,
          showType: this.imgListType,
        }, res => {
          if (res.data.code == 200) {
            if (options.clearImageList) {
              this.showImgList = [];
            }
            if (res.data.data && res.data.data.resultData) {
              let _pageList = res.data.data.resultData;
              this.showImgList = this.showImgList.concat(_pageList);
              // this.showImgList.forEach((value,index) => {
              //   // console.log('value', value.imageUrl);
              //   // value.imgFlag = false;
              //   // this.preLoadImg(value,index)
              //
              // });
              this.photoTotal = res.data.data.pageData.countNum; // 总数量
              if (_pageList.length > 0) {
                this.indexPage++;
                this.loading = false;
              } else {
                this.loading = true;
                this.loadingText = '我是有底线的';
              }
            } else {
              this.loading = true;
              this.loadingText = '我是有底线的';
            }
          } else {
            this.loading = false;
          }
        });
      },
      //TODO: 获取用户抽奖状态(时间、次数)
      getUserLotteryStatus() {
        let url = '/api.php/api/Api/luckyDrawTime';
        this.$ajax(url, {}, res => {
          if (res.data.code == 200) {
            this.remainDrawLotteryNum = localStorage.getItem('RemainDrawLotteryNum');
            if (this.remainDrawLotteryNum == null || this.remainDrawLotteryNum >= res.data.data.times) {
              this.remainDrawLotteryNum = res.data.data.times;
              this.currentLotteryTime = res.data.data.time;
              localStorage.setItem('RemainDrawLotteryNum',this.remainDrawLotteryNum);
            }else if(this.remainDrawLotteryNum > 0) {
              this.currentLotteryTime = this.lotteryTimeList[this.remainDrawLotteryNum];
            }else {
              this.currentLotteryTime = 0;
              this.showLotteryTime.minutes = '--';
              this.showLotteryTime.seconds = '--';
            }
            if (this.currentLotteryTime > 0 && this.remainDrawLotteryNum > 0) {
              this.startCountDown();
            }
          }
        });
      },
      getPublicAddress() {
        let url = '/api.php/api/Api/publicAddress';
        this.$ajax(url, {}, res => {
          if (res.data.code == 200) {
            this.publicInfo.name = res.data.data.name;
            this.publicInfo.url = res.data.data.link;
            console.log('this.publicInfo',this.publicInfo);
          }
        });
      },
      getFootInfo() {
        let url = '/api.php/api/Api/footInfo';
        this.$ajax(url, {}, res => {
          if (res.data.code == 200) {
            this.qRcodeInfo.qrContent = res.data.data.qrContent;
            this.qRcodeInfo.qrLink = res.data.data.qrLink;
            this.qRcodeInfo.content = [...res.data.data.content];
            console.log('this.qRcodeInfo',this.qRcodeInfo);
          }
        });
      },
      startCountDown() {
        if (this.lotteryTimer) {
          clearInterval(this.lotteryTimer);
          this.lotteryTimer = null;
        }
        this.lotteryTimer = setInterval(()=>{
          this.resetTimer(this.currentLotteryTime);
          this.currentLotteryTime -= 1000;
        },1000);
      },
      resetTimer(time) {
        let showTime = this.showLotteryTime;
        if (time >= 0) {
          showTime.days = this.checkTime(parseInt(time / 1000 / 60 / 60 / 24, 10)); //计算剩余的天数
          showTime.hours = this.checkTime(parseInt(time / 1000 / 60 / 60 % 24, 10)); //计算剩余的小时
          showTime.minutes = this.checkTime(parseInt(time / 1000 / 60 % 60, 10)); //计算剩余的分钟
          showTime.seconds = this.checkTime(parseInt(time / 1000 % 60, 10)); //计算剩余的秒数
        }else {
          clearInterval(this.lotteryTimer);
          this.currentLotteryTime = 0;
          this.canDrawLotteryNum = 1;
          localStorage.setItem('CanDrawLotteryNum',this.canDrawLotteryNum);
          this.lotteryTimer = null;
        }
        // console.log('showTime',showTime);
        // console.log('this.showLotteryTime',this.showLotteryTime);
      },
      checkTime(i) {
        return  i < 10 ? "0" + i : i;
      },
      getVideoData() {
        let url = '/api.php/api/Api/getLiveUrl';
        this.$ajax(url, {}, res => {
          if (res.data.code == 200) {
            this.videoObj.type = res.data.data.type; // type live image
            this.videoObj.protocolType = res.data.data.agreement;
            this.videoObj.src = res.data.data.linkUrl;
            console.log('this.videoObj',this.videoObj);
            //TODO: 自动播放 微信暂不支持
            // this.$nextTick(()=>{
            //     document.querySelector('#liveVideo').play();
            //     this.weixinPlay(function() {
            //         document.querySelector('#liveVideo').play();
            //     })
            // });
          } else {
            // TODO: 目前暂无直播
          }
        });
      },
      getWatchNumbers() {
        let url = '/api.php/api/Api/getNumber';
        this.$ajax(url, {}, res => {
          if (res === null) {
            this.watchPoll(this.wacthErrorTime);
          }
          if (res.data.code == 200) {
            this.watchNumbers = res.data.data.number;
            this.watchPoll(this.watchDelayTime);
          } else {
            // TODO: 目前暂无直播
            this.watchPoll(this.watchDelayTime);
          }
        });
      },
      watchPoll(time) {
        if (this.watchTimer) {
          clearTimeout(this.watchTimer);
          this.watchTimer = null;
        }
        this.watchTimer = setTimeout(this.getWatchNumbers, time);
      },
      newUpload() {
        this.imgListType = 'new';
        this.indexPage = 1;
        if (this.imgListType === this.lastImgListType) {
          return false;
        }
        this.lastImgListType = this.imgListType;
        this.fetchData();
        this.$refs.share_content_c.scrollTop = 0;
      },
      timeSort() {
        if (this.imgListType === 'new') {
          this.imgListType = 'asc';
          this.lastImgListType = this.imgListType;
          this.indexPage = 1;
          this.fetchData();
        } else {
          this.imgListType = this.imgListType === 'asc' ? 'desc' : 'asc';
          this.lastImgListType = this.imgListType;
          this.showImgList.reverse();
        }
        this.$refs.share_content_c.scrollTop = 0;
      },
      closeImgShow() {
        this.showVideoFlag = true;
        this.bigImgShow = false;
      },
      showImg(item) {
        this.showVideoFlag = false;
        this.bigImgShow = true;
        this.bigImgSrc = item.imageUrl;
      },
      returnTop() {
        let $share_content_c = this.$refs.share_content_c;
        let returnTopTimer = setInterval(()=>{
          let scrollTop = $share_content_c.scrollTop;
          let speed = scrollTop / 40;
          if (scrollTop === 0) {
            clearInterval(returnTopTimer)
          }
          $share_content_c.scrollTop -= speed;
        });
        // (function smoothscroll() {
        //   let currentScroll = document.documentElement.scrollTop || document.body.scrollTop;
        //   if (currentScroll > 0) {
        //     window.requestAnimationFrame(smoothscroll);
        //     window.scrollTo(0, currentScroll - (currentScroll / 5));
        //   }
        // })();
      },
      wxOnShare() {
        let wx_config = {};
        let url = '/api.php/api/Api/weChatShare';
        let data = encodeURIComponent(window.location.href.split('#')[0]);
        this.$ajax(url, {url: data}, res => {
          if (res.data.code == 200) {
            wx_config = res.data.data;
            wx.config({
              debug: false,
              appId: wx_config.corpid, // 必填，公众号的唯一标识
              timestamp: wx_config.timestamp, // 必填，生成签名的时间戳
              nonceStr: wx_config.nonceStr, // 必填，生成签名的随机串
              signature: wx_config.signature,// 必填，签名
              jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline', 'onMenuShareQQ', 'onMenuShareQZone'],
            });
            wx.ready(function () {
              let title = wx_config.title;
              let desc = wx_config.content;
              let link = wx_config.link;
              let imgUrl = wx_config.imgUrl;

              // 分享给朋友
              wx.onMenuShareAppMessage({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                imgUrl: imgUrl, // 分享图标
                type: '', // 分享类型,music、video或link，不填默认为link
                dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
                success: function (res) {
                },
                cancel: function (res) {
                },
              });

              // 分享到朋友圈
              wx.onMenuShareTimeline({
                title: title,
                desc: desc,
                link: link,
                imgUrl: imgUrl,
                success: function (res) {
                },
                cancel: function (res) {
                },
              });

              // 分享到QQ
              wx.onMenuShareQQ({
                title: title,
                desc: desc,
                link: link,
                imgUrl: imgUrl,
                success: function (res) {
                },
                cancel: function (res) {
                },
              });

              // 分享到QQ空间
              wx.onMenuShareQZone({
                title: title,
                desc: desc,
                link: link,
                imgUrl: imgUrl,
                success: function (res) {
                },
                cancel: function (res) {
                },
              });
            });

            //处理验证失败的信息
            wx.error(function (res) {
              console.log('验证失败返回的信息:', res);
            });
          }
        });
      },
      showTurntable() {if (this.canDrawLotteryNum) { this.turntableFlag = true; }},
      closeTurntable() { this.turntableFlag = false; },
      showSubmitPrompt() { this.submitPromptFlag = true; },
      closeSubmitPrompt() { this.submitPromptFlag = false; },
      showlotteryPrompt() { this.lotteryPromptFlag = true; },
      closelotteryPrompt() { this.lotteryPromptFlag = false; },
      resetLotteryView() {
        this.canDrawLotteryNum = Number(localStorage.getItem('CanDrawLotteryNum')) || 0;
        this.showLotteryTime.minutes = '--';
        this.showLotteryTime.seconds = '--';
        this.getUserLotteryStatus();
      },
      // 暂未用到
      preLoadImg(obj,index) {
        let img = new Image();
        img.onload = ()=> {
          obj.imgFlag = true;
          Vue.set(this.showImgList,index,obj)
          // this.$set(obj,index,this.showImgList[index])
          // this.showImgList.forEach(value => {
          //     if(value.imgUrl === obj.imgUrl) {
          //       value.imgFlag = true;
          //     }
          //   })
        };
        img.src = obj.imageUrl;
      },
      weixinPlay(fn) {
        if (window.WeixinJSBridge) {
          window.WeixinJSBridge.invoke('getNetworkType', {}, fn);   // 这句话是在微信中可以自动播放的核心
        } else {
          document.addEventListener('WeixinJSBridgeReady', function () {
            window.WeixinJSBridge.invoke('getNetworkType', {}, fn);
          });
        }
      },
      videoEnd() {
        // TODO: 检测视频结束
      },
      // monitorReturnTop() {
      //   if (this.$refs.share_content_c.scrollTop > 340) {
      //     this.returnTopFlag = true;
      //   }else {
      //     this.returnTopFlag = false;
      //   }
      //   window.onscroll = (e) => {
      //     let currentScroll = document.documentElement.scrollTop || document.body.scrollTop;
      //     this.returnTopFlag = currentScroll >= this.returnTopHeight ? true : false;
      //   };
      // },
    },
    watch: {},
    mounted() {
      // TODO: 初始化 PC 端加载慢 视频不能播放导致
      this.init();
    },
    components: {
      MugenScroll,
      Turntable,
      SubmitPrompt,
      LotteryPrompt
    },
  };
</script>

<style lang="less" scoped>
    #mainPage {
        width: 7.5rem;
        height: 34rem;
        margin: 0 auto;
        background: url("../assets/img/mainBackgroud.png") no-repeat center center;
        background-size: cover;
        position: relative;
        .header {
            overflow: hidden;
            .headerLogoC {
                margin-top: 0.23rem;
                padding: 0 0.22rem 0 0.34rem;
                /*background-color: cyan;*/
                display: flex;
                justify-content: space-between;
                img.leftLogo {
                    width: 1.59rem;
                    height: 1.07rem;
                }
                img.rightLogo {
                    width: 1.85rem;
                    height: 1.05rem;
                }
            }

        }
        .liveC {
            margin-top: 6.19rem;
            color: #FBCD8B;
            /*background-color: cyan;*/
            .liveTitleC {
                width: 6.57rem;
                height: 0.28rem;
                margin: 0 auto 0.2rem;
                background: url("../assets/img/titleBack_live.png") no-repeat center center;
                background-size: cover;
                text-align: center;
                display: flex;
                align-items: center;
                justify-content: center;
            }
            .online_text {
                font-size: 0.2rem;
