            align-items: center;
            justify-content: center;
            z-index: 99;
            img {
                width: 90%;
            }
        }
        .returnTop {
            position: absolute;
            width: 0.6rem;
            height: 0.6rem;
            line-height: 0.6rem;
            top: 22rem;
            right: 0.54rem;
            z-index: 10;
            background-color: rgba(183,0,9,1);
            border-radius: 0.6rem;
            border: 2px solid rgba(249,189,120,1);
            text-align: center;
            color: rgba(249,189,120,1);
            box-shadow: 0px 2px 4px 1px #121213;
            /*width: 0.87rem;*/
            /*height: 0.89rem;*/
            /*background: url("../assets/img/returnIcon.png") no-repeat center center;*/
            /*background-size: cover;*/
        }
